import * as React from "react";
import { IOfferData, IUserData } from "../config/appdata";
import { Logging } from "../logging/logging";
import { GetEnvConfig } from "../config/GetEnvConfig";
import { Icon } from "office-ui-fabric-react";
import { CommonService } from '../findItBL/commonservice';


/**
 * State for the Tile View React component
 */
export interface ICustumTileSate {
    ShowAddToSEETeam: boolean;
    OfferResult?: IOfferData[];
    PageSize?: number;
    PageNo?: number;
    Index?: number;
}

/**
 * Properties for the Tile View React component
 */
export interface ICustumTileProps {
    offerResult?: IOfferData[];
    pageSize?: number;
    pageNo?: number;
    onAddToSEETeamClick(e: any, selectedData: IOfferData, showView: boolean): any;
}

/**
 * Implementation of the Tile View React Component
 */
export class CustumTileViewCompassOne extends React.Component<ICustumTileProps, ICustumTileSate> {
    //to declare the class variable
    private tileContainer: any = null;
    private fileName: string = "CustumTileViewDocs.tsx";
    private commonService: CommonService;
    //constructor to initiiaise the state variable.    
    constructor(props: ICustumTileProps) {
        super(props);
        this.tileContainer = React.createRef();
        this.state = {
            ShowAddToSEETeam: false
            , OfferResult: this.props.offerResult
            , PageNo: this.props.pageNo
            , PageSize: this.props.pageSize
            , Index: -1
        };
        this.commonService = new CommonService();
    }

    //to set the state variable from the parent props
    public componentWillReceiveProps(nextProps: any) {
        this.setState({
            OfferResult: nextProps.offerResult, PageSize: nextProps.pageSize
            , PageNo: nextProps.pageNo
        });
    }

    //to register the mouse click event 
    public componentDidMount() {
        document.addEventListener("mousedown", this.handleClickOutside);
    }

    //to un-register the mouse click event 
    public componentWillUnmount() {
        document.removeEventListener("mousedown", this.handleClickOutside);
    }

    public handleFlip(SrNo: number, title: string) {
        try {
            let tile = document.getElementById(`compassTile_${SrNo}`);
            const flipStyle = "rotateY(180deg)";
            if (tile != null && tile != undefined) {
                if (tile.style.transform == flipStyle) {
                    tile.style.transform = "none";
                }
                else {
                    tile.style.transform = flipStyle;
                }
            }
            Logging.AppInsightsTrackEvent(GetEnvConfig.AppSettings.appName, "Information", "Flipped Compass Tile", `Flipped Compass Tile: ${title}`, "");
        }
        catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at handleFlip", error);
        }

    }


    //to render the Tile View UI
    public render() {
        let currOfferResult = this.state.OfferResult === undefined ? [] : this.state.OfferResult;
        const pageSize = this.state.PageSize === undefined ? 10 : this.state.PageSize;
        const pageNo = this.state.PageNo === undefined ? 1 : this.state.PageNo;
        currOfferResult = currOfferResult.filter(obj => obj.SrNo >= ((pageSize * (pageNo - 1) + 1))
            && obj.SrNo <= (pageSize * pageNo));
        let tileViewUI: any = null;
        if (currOfferResult && currOfferResult.length > 0) {
            tileViewUI = currOfferResult.map((item, index) => {
                let salesStage = "Not Available";
                let salesStagePercentage = "0%";
                if (item.SalesStage != null && item.SalesStage != undefined && item.SalesStage.split(" ").length == 2) {
                    [salesStage, salesStagePercentage] = item.SalesStage.split(" ");
                }

                // deal owner
                let dealOwner: IUserData[] = item.UserData.filter(x => x.role.toLowerCase().includes("deal owner"));

                return (
                    <div className="compassOneTileView flip-card" style={{ display: "inline-flex" }} key={item.SrNo}>
                        <div className="flip-card-inner tile-contr compassOne_tiles" id={`compassTile_${item.SrNo}`}>
                            <div className="flipBoxcntr">
                                <div className="tile-header ">
                                    <span className="flipIcon" tabIndex={0} title="Click here for back View" onClick={() => this.handleFlip(item.SrNo, item.Title)}></span>
                                </div>

                                <div className="CompassOnetileInner">
                                    <a title={item.Title} href={item.URL} rel="noreferrer" target="_blank" className="outline OfferFocus ssOfferTitleValueClassName" >
                                        <span className="titlewithImage">
                                            <p className="truncate-2lines">{item.Title}</p>
                                        </span>
                                    </a>

                                    <div className="compassOnedocs">
                                        {item.Docs && (item.Docs.length > 1 || (item.Docs.length == 1 && item.Docs[0].OID > 0)) ?
                                            <ul className="rclp-links CompusoneDocDetails">
                                                {
                                                    item.Docs && item.Docs.length > 0 ?
                                                        item.Docs.map(o => {
                                                            return o.OID > 0 ?
                                                                <li title={o.Title}>
                                                                    <a className="ssOfferTileDocumentsClassName" title={o.Title} href={o.DocURL} rel="noreferrer" target="_blank" >
                                                                        <img alt="PowerPoint document" src={`../assets/${this.commonService.getFileTypeIcon(o.Title)}`} />
                                                                    </a></li> : null;
                                                        })

                                                        : null
                                                }
                                            </ul> :
                                            <p className="truncate-Singleline nodataClass"> No Documents </p>
                                        }
                                    </div>
                                    <div className="CompassOneTilemiddle">
                                        <p>Oppty : <span>{item.OpportunityID}</span></p>
                                        <p>Sales Stage : <span>{salesStage}</span></p>
                                    </div>
                                    <div className="NewtileBottom">
                                        <div className="meter">
                                            <span style={{ width: `${salesStagePercentage}` }} title={salesStagePercentage}>
                                                <b className="progressBar"></b>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="flipBoxBack flipBoxcntr">
                                <div className=" tile-contr compassOne_tiles">
                                    <div className="tile-header ">
                                        <span className="flipIcon" tabIndex={0} title="Click here for front View" onClick={() => this.handleFlip(item.SrNo, item.Title)}></span>
                                    </div>
                                    <div className="compassOneBackContent">
                                        <p>Deal Owners {item.UserData.length > 0 &&  item.UserData.find((obj, index) => (obj.role.toLowerCase().includes("deal owner"))) != undefined? "" : <span>Not Available</span>}</p>
                                        <ul>
                                            <li>
                                                {
                                                    item.UserData.filter((obj, index) => (obj.role.toLowerCase().includes("deal owner"))).map((x, index) => index < 5 ?
                                                        <a title={`View Profile in Delve: ${x.name}`} href={x.acctName} rel="noreferrer" target="_blank"><img src={`${GetEnvConfig.AppSettings.SharepointUrl}/_layouts/15/userphoto.aspx?accountname=${x.email}`} style={{ height: '33px', width: "33px" }} alt={x.name} onError={this.onImageNotFound} /></a>
                                                        : null
                                                    )}
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            });
        }
        return (
            <>
                {tileViewUI}
            </>
        );
    }

    public onImageNotFound = (event: any) => {
        try {
            event.target.src = "../assets/UserSilhouette.png";
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onImageNotFound() in CustumTileViewCompassOne.tsx" + this.fileName, error);
        }
    }


    //to handle the click out side event 
    public handleClickOutside = (event: any) => {
        try {
            if (this.tileContainer.current && !this.tileContainer.current.contains(event.target)) {
                if (this.state.ShowAddToSEETeam) {
                    this.setState({
                        ShowAddToSEETeam: false,
                    });
                }
            }
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at handleClickOutside() in CustumTileViewCompassOne.tsx" + this.fileName, error);
        }
    }

    //to show the Add to SEE Team popup 
    public onShowAddToSEETeamClick(e: any, currIndex: number) {
        try {
            this.setState({
                ShowAddToSEETeam: true,
                Index: currIndex
            });
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onShowAddToSEETeamClick() in CustumTileViewCompassOne.tsx" + this.fileName, error);
        }
    }

    //to show the Add to SEE Team Menu click 
    public onShowAddToSEETeamMenuClick(e: any, index: number, item: IOfferData) {
        try {
            e.stopPropagation();
            e.preventDefault();
            this.setState({
                ShowAddToSEETeam: false,
                Index: -1
            });
            this.props.onAddToSEETeamClick(e, item, true);
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onShowAddToSEETeamMenuClick() in CustumTileViewCompassOne.tsx" + this.fileName, error);
        }
    }
}
