import { Settings as Settings_uat } from './uatcommonconfig';
import { Settings as Settings_stg } from './stgcommonconfig';
import { Settings as Settings_prod } from './prodcommonconfig';

export class GetEnvConfig {
    public static AppSettings: any;

    constructor() {
        const url = window.location.href;

        if (url.toLowerCase().indexOf("https://cpsui-dev.") > -1
            || url.toLowerCase().indexOf("/localhost:") > -1
        ) {
            GetEnvConfig.AppSettings = Settings_uat.AppSettings;
        }
        else if (url.toLowerCase().indexOf("https://cpsui-stg.") > -1
            || url.toLowerCase().indexOf("https://campuscognitiveservicedevapp2") > -1) {
            GetEnvConfig.AppSettings = Settings_stg.AppSettings;
        }
        else if (url.toLowerCase().indexOf("https://cpsui-uat.") > -1) {
            GetEnvConfig.AppSettings = Settings_uat.AppSettings;
        }
        else if (url.toLowerCase().indexOf("https://cpsui-extuat.") > -1) {
            GetEnvConfig.AppSettings = Settings_uat.AppSettings;
        }
        else {
            GetEnvConfig.AppSettings = Settings_prod.AppSettings;
        }
    }


    public static IsLowerEnvEnvronment() {
        const url = window.location.href;
        return url.toLowerCase().indexOf("https://cpsui-dev.") > -1
            || url.toLowerCase().indexOf("/localhost:") > -1
            || url.toLowerCase().indexOf("https://campuscognitiveservicedevapp2") > -1
            || url.toLowerCase().indexOf("https://cpsui-stg.") > -1
            || url.toLowerCase().indexOf("https://cpsui-uat.") > -1
            || url.toLowerCase().indexOf("https://cpsui-extuat.") > -1
    }
}