    //to handle the Add to SEE team click 
    public async onAddToSEETeamClick(e: any, selectedData: IOfferData, showView: boolean, type?: string) {
        try {
            if (this.state.showAddToSEETeams != showView) {
                if (showView) {
                    if (this.state.addToSEETeamsRef && this.state.addToSEETeamsRef.OID == selectedData.OID) {
                        this.setState({ showAddToSEETeams: showView });
                    }
                    else {
                        if (this.state.joinedTeams && this.state.joinedTeams.length > 0) {
                            this.setState({
                                isTeamLoading: true, showAddToSEETeams: showView
                                , isSavingSEETeams: false
                            }, () => {

                                if (GetEnvConfig.IsLowerEnvEnvronment()) {
                                    this.authService = new AuthService();
                                    this.authService.getTokenWithLoginPopup("").then(
                                        response => {
                                            if (response) {
                                                this.graphService = new GraphService();
                                                this.graphService.getAddToSEETeamsInfo(selectedData.OID, selectedData.Title
                                                    , response, type).then(obj => {
                                                        this.setState({
                                                            addToSEETeamsRef: obj, isTeamLoading: false
                                                            , showAddToSEETeams: showView, isTeamCreated: false
                                                        });
                                                    });
                                            }
                                        });
                                }
                                else {
                                    this.ssoSearvice = new SSOSearvice();
                                    this.ssoSearvice.getAccessTokenClient().then((clientSideToken) => {
                                        this.ssoSearvice.getAccessTokenServer(clientSideToken, "").then((response) => {
                                            if (response) {
                                                this.graphService = new GraphService();
                                                this.graphService.getAddToSEETeamsInfo(selectedData.OID, selectedData.Title
                                                    , response, type).then(obj => {
                                                        this.setState({
                                                            addToSEETeamsRef: obj, isTeamLoading: false
                                                            , showAddToSEETeams: showView, isTeamCreated: false
                                                        });
                                                    });
                                            }
                                        });
                                    });
                                }
                            });

                        }
                        else {
                            this.setState({ isTeamLoading: true, showAddToSEETeams: showView, isSavingSEETeams: false }, () => {

                                if (GetEnvConfig.IsLowerEnvEnvronment()) {
                                    this.authService = new AuthService();
                                    this.authService.getTokenWithLoginPopup("graph").then(
                                        graphResponse => {
                                            if (graphResponse) {
                                                this.graphService.getJoinedTeams(graphResponse).then(joinedTeamObj => {
                                                    if (joinedTeamObj && joinedTeamObj.length > 0) {
                                                        this.authService.getTokenWithLoginPopup("").then(
                                                            response => {
                                                                this.graphService.getAddToSEETeamsInfo(selectedData.OID, selectedData.Title, response, type).then(seeTeamsObj => {
                                                                    this.setState({ isTeamLoading: false, joinedTeams: joinedTeamObj, isTeamCreated: false, addToSEETeamsRef: seeTeamsObj, showAddToSEETeams: showView });
                                                                });


                                                            });
                                                    } else
                                                        this.setState({ showAddToSEETeams: showView, isTeamLoading: false });
                                                });
                                            }
                                        });
                                }
                                else {
                                    this.ssoSearvice = new SSOSearvice();
                                    this.ssoSearvice.getAccessTokenClient().then((clientSideToken) => {
                                        this.ssoSearvice.getAccessTokenServer(clientSideToken, "graph").then((graphResponse) => {
                                            if (graphResponse) {
                                                this.graphService.getJoinedTeams(graphResponse).then(joinedTeamObj => {
                                                    if (joinedTeamObj && joinedTeamObj.length > 0) {
                                                        this.ssoSearvice.getAccessTokenClient().then((clientSideToken) => {
                                                            this.ssoSearvice.getAccessTokenServer(clientSideToken, "").then((response) => {
                                                                this.graphService.getAddToSEETeamsInfo(selectedData.OID, selectedData.Title, response, type).then(seeTeamsObj => {
                                                                    this.setState({ isTeamLoading: false, joinedTeams: joinedTeamObj, isTeamCreated: false, addToSEETeamsRef: seeTeamsObj, showAddToSEETeams: showView });
                                                                });
                                                            });
                                                        });
                                                    } else
                                                        this.setState({ showAddToSEETeams: showView, isTeamLoading: false });
                                                });
                                            }
                                        });
                                    });

                                }

                            });
                        }
                    }
                }
            }
            else
                this.setState({ showAddToSEETeams: showView });

        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at onAddToSEETeamClick() in ${this.fileName}`, error);
        }
    }

