//import { getFadedOverflowStyle } from "@fluentui/style-utilities";
import * as React from "react";
import { Logging } from "../logging/logging";
import { GetEnvConfig } from "../config/GetEnvConfig";

/**
 * State for the CustumDropdown React component
 */
export interface ICustumDropdownState {
    open?: boolean;
    dataSource: IDataSource;
    isMoreOn: boolean;
}

/**
 * Properties for the CustumDropdown React component
 */
export interface ICustumDropdownProps {
    dataSource: IDataSource;
    keyID: string;
    onApplyClick(e: any, selectedData: IData[]): any;
}

export interface IDataSource {
    title: string;
    text: string;
    value: string;
    searchText: string;
    data: IData[];
}

export interface IData {
    text: string;
    value: string;
    selected: boolean;
}
/**
 * Implementation of the Dropdown Component
 */
export class CustumOpenDropdown extends React.Component<ICustumDropdownProps, ICustumDropdownState> {
    //to declare the class variable   
    private container: any = null;
    private filterName: string = "";

    //constructor to initiiaise the state variable.
    constructor(props: ICustumDropdownProps) {
        super(props);
        this.container = React.createRef();
        this.state = {
            open: false,
            isMoreOn: false,
            dataSource: props.dataSource || { title: "", text: "", value: "", searchText: "", data: [] }
        };

        this.filterName = this.props.dataSource.title;
    }

    //to set the state variable from the parent props
    public componentWillReceiveProps(nextProps: any) {
        this.setState({ dataSource: nextProps.dataSource });
    }

    //to  register the mouse click event
    public componentDidMount() {
        document.addEventListener("mousedown", this.handleClickOutside);
    }

    //to un-register the mouse click event
    public componentWillUnmount() {
        document.removeEventListener("mousedown", this.handleClickOutside);
    }


    //to render the Open dropdown UI
    public render() {
        let filterList = this.state.dataSource && this.state.dataSource.data ?
            this.state.dataSource.searchText && this.state.dataSource.searchText.trim().length > 0
                ? this.state.dataSource.data.filter(obj => obj.text.toLowerCase().includes(this.state.dataSource.searchText.toLowerCase()))
                : this.state.dataSource.data : [];
        const renderList = this.getRenderList();
        return (
            <div className="refiners-section" title={this.state.dataSource.title}>
                <h3>{this.state.dataSource.title}</h3>
                <div className="search-contr" style={{ display: "flex" }}>
                    <i className="ms-Icon ms-Icon--Search x-hidden-focus" title="Search" aria-hidden="true"></i>
                    <input onChange={(e) => this.onSearchTextChange(e)} type="text"
                        aria-label="Search" value={this.state.dataSource.searchText}
                        className="AddSearchItem" placeholder="Search" />
                    <i className="ms-Icon ms-Icon--ChromeClose"
                        title="" aria-hidden="true"></i>
                </div>
                <ul className="refiners-ul">
                    {
                        renderList
                    }
                </ul>
                {filterList && filterList.length > 5 ?
                    <div className="SeeMore">
                        <a title="See More" className="RefinerSeeMore" onClick={(e) => this.onMoreClick(e)}>
                            {this.state.isMoreOn ?
                                <> <i className="arrowDown"></i> <span className="seeMoreText" style={{ color: "#a6a6a" }}>Less</span></>
                                :
                                <> <i className="arrowDown"></i> <span className="seeMoreText">More</span></>
                            }

                        </a>
                    </div>
                    : null}
                <div className="refiners-clear-apply" data-subscope="Documents" data-parent="Domain">
                    <div title="Domain Clear" className="vrclear clear-ly" data-subscope="Documents" data-parent="Domain">
                        <i onClick={(e) => this.onClearAllClick(e)}
                            className="ms-Icon ms-Icon--ChromeClose" title="ChromeClose" aria-hidden="true"></i>
                        <span
                            onClick={(e) => this.onClearAllClick(e)}> Clear</span>
                    </div>
                    <span title="Domain Apply" className="vrapply" data-subscope="Documents" data-parent="Domain"
                        onClick={(e) => this.onApplyClick(e)}>Apply</span>
                </div>
            </div>

        );
    }

    //to handle the more click event
    private onMoreClick(e: any) {
        try {
            e.stopPropagation();
            e.preventDefault();
            this.setState({ isMoreOn: !this.state.isMoreOn });
            Logging.AppInsightsTrackEvent(GetEnvConfig.AppSettings.appName, "Information", "Expand/Collapse Filter list", `${this.state.isMoreOn ? 'Expanded' : "Collapsed"} ${this.state.dataSource.title} filter`, "");
        }
        catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onMoreClick() in CustumOpenDropdown.tsx", error);
        }
    }
    //to return the render list on the UI
    private getRenderList() {
        let filterList = this.state.dataSource && this.state.dataSource.data ?
            this.state.dataSource.searchText && this.state.dataSource.searchText.trim().length > 0
                ? this.state.dataSource.data.filter(obj => obj.text.toLowerCase().includes(this.state.dataSource.searchText.toLowerCase()))
                : this.state.dataSource.data : [];
        filterList = [...filterList];
        if (!this.state.isMoreOn && filterList.length > 5) {
            filterList = filterList.slice(0, 5);
        }

        const renderList = filterList && [...filterList].map((item, i) => {
            return <li>
                <div className="divCheckBoxFilter">
                    <input
                        className="styled-checkbox"
                        id={this.props.keyID + i}
                        aria-label="Select a option in vertical refiners Apps"
                        type="checkbox"
                        title={item.text}
                        checked={item.selected}
                        onClick={(e) => this.onChkClick(e, item)}
                        value={item.value}
                    />
                    <label htmlFor={this.props.keyID + i} style={{ display: "block" }}
                        className="CheckBoxFilter WrapFilterText" title="Apps">{item.text} </label>
                    {item.selected ? <div className="testing1" onClick={(e) => this.onChkClick(e, item)}></div> : null}

                </div>
            </li>;

        });
        return renderList;
    }

    //to handle the click out side event
    public handleClickOutside = (event: any) => {
        if (this.container.current && !this.container.current.contains(event.target)) {
            this.setState({
                open: false,
            });
        }
    }

    //to handle the dropdown click event
    public onDDLClick(e: any) {
        this.setState({
            open: !this.state.open
        });
    }

    //to handle the checkbox click event
    public onChkClick(e: any, item: IData) {
        try {
            e.stopPropagation();
            e.preventDefault();
            const currObj = [...this.state.dataSource.data].find(obj => (obj.text.toLowerCase() === item.text.toLowerCase()
                && obj.value.toLowerCase() === item.value.toLowerCase()));
            if (currObj) {
                if (this.state.dataSource.title == "Organisation") {
                    this.singleSelect();
                }
                currObj.selected = !currObj.selected;
                this.setState({ dataSource: this.state.dataSource });
            }
        }
        catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onChkClick() in CustumOpenDropdown.tsx", error);
        }
        //Logging.AppInsightsTrackEvent(GetEnvConfig.AppSettings.appName,"Information", "Select Filter Checkbox" ,`Selected ${this.filterName} filter: ${item.value}`, "");
    }

    //to handle the clear all click event
    public onClearAllClick(e: any) {
        try {
            e.stopPropagation();
            e.preventDefault();
            this.state.dataSource.data.forEach(obj => (obj.selected = false));
            this.setState({ dataSource: this.state.dataSource });

            Logging.AppInsightsTrackEvent(GetEnvConfig.AppSettings.appName, "Information", "Clear Filter", `Clear ${this.filterName} filter`, "");
        }
        catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onClearAllClick() in CustumOpenDropdown.tsx", error);
        }
    }

    //to handle the apply click event
    public onApplyClick(e: any) {
        try {
            e.stopPropagation();
            e.preventDefault();
            const selectedData = this.state.dataSource.data.filter(obj => (obj.selected === true));
            this.setState({ open: false }, () => {
                this.props.onApplyClick(e, selectedData);
            });

            const selectedFilters = selectedData.map(x => x.value);
            Logging.AppInsightsTrackEvent(GetEnvConfig.AppSettings.appName, "Information", "Apply Filter", `Selected ${this.filterName} Filters; ${selectedFilters}`, "");
        }
        catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onClearAllClick() in CustumOpenDropdown.tsx", error);
        }
    }

    //to handle the search text change event
    public onSearchTextChange(e: any) {
        this.state.dataSource.searchText = e.target.value;
        this.setState({ dataSource: this.state.dataSource });
    }

    public singleSelect() {
        try {
            this.state.dataSource.data.forEach(obj => (obj.selected = false));
        }
        catch (err) {

        }
    }
}
