import * as React from "react";
import { IDocument, SSUXType } from "../config/appdata";
import { CommonService } from ".././findItBL/commonservice";
import { GetEnvConfig } from "../config/GetEnvConfig";
import { Logging } from "../logging/logging";
/**
 * State for the Child Grid View React component
 */
export interface ICustumChildGridSate {
}

/**
 * Properties for the Child Grid View React component
 */
export interface ICustumChildGridProps {
    rowData: IDocument;
    ssuxType: string;
}
/**
 * Implementation of the Child Grid View component
 */
export class CustumGridChild extends React.Component<ICustumChildGridProps, ICustumChildGridSate> {
    //constructor to initiiaise the state variable.
    private commonService: CommonService;
    constructor(props: ICustumChildGridProps) {
        super(props);
        this.state = {
        };
    }

    //to render the child grid controls.
    public render() {
        this.commonService = new CommonService();
        return (<><div className="new-table-child-row childKmItemInfo Pagination-List-Items-Offer-Docs-1561" role="row">
            <div className="new-table-row kmItemInfo">
                <div className="newWrapper new-attributes">
                    <div className="newWrapper new-title-comment-module-reporter">
                        <div className="newWrapper new-title-comment">
                            <div className="new-column newTitle new-act-td">
                                <span className="docicontype" role="cell">
                                {
                                    this.props.ssuxType == SSUXType.virtuoso ? <img src={`${GetEnvConfig.AppSettings.SharepointUrl}/_layouts/15/userphoto.aspx?accountname=${this.props.rowData.email}`} style={{height: '33px', width: "33px", borderRadius: "50%"}} onError={this.onImageNotFound}/>: <img src={`../assets/${this.commonService.getFileTypeIcon(this.props.rowData.DocURL)}`} />
                                }
                                </span>
                                <a target="_blank"
                                    rel="noreferrer"
                                    className="kmdocumenturl kmChildItemTitle ssOfferListViewDocumentTitleClassName"
                                    href={this.props.rowData.DocURL} aria-label={`${this.props.rowData.Title}`} role="cell">
                                    <span aria-label="opens in a new tab">{this.props.rowData.Title}</span>
                                </a>
                                <div className="hovercardTooltiptext">
                                    <h4 title={this.props.rowData.Title}
                                        className="HoverCard">{this.props.rowData.Title}</h4>
                                    <span className="tooltipdescription">
                                        <p title="" className=""></p>
                                    </span>
                                    <span className="tooltipBottom">
                                        <span className="tooltipBottomLinks">
                                            <a href="#"><span className="OpenInOse"></span></a>
                                            <a href="#"><span className="OpenInCampus"></span></a>
                                            <a href="#"><span className="OpenInVirtuoso"></span></a>
                                            <a href="#"><span className="OpenInCompassOne"></span></a>
                                        </span>
                                        <a className="tooltipOpenLink" target="_blank"
                                            rel="noreferrer"
                                            href={this.props.rowData.DocURL}>
                                            <span className="openLink " title="Open ">Open</span></a>
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {
                    this.props.ssuxType == SSUXType.ipkit || this.props.ssuxType == SSUXType.project ?
                        <div className="newWrapper" role="cell">
                            <div title={String(this.props.rowData.views)} className="new-column views new-act-td">{this.props.rowData.views}</div></div>
                        : null
                }
                {
                    this.props.ssuxType != SSUXType.virtuoso ? <div className="newWrapper " role="cell">
                    <div className="new-column modified new-act-td ">
                        {this.commonService.ssListViewDateFormatforChild(this.props.rowData.lastModifed)}
                    </div>
                </div>: null
                }
                {
                    this.props.ssuxType == SSUXType.virtuoso ? <div className="newWrapper" role="cell">
                        <div title={this.props.rowData.jobTitle} className="new-column title new-act-td">{this.props.rowData.jobTitle}</div></div>
                        : null
                }
                <div className="newWrapper "></div>
            </div>
        </div>
        </>
        );
    }

    
    public onImageNotFound = (event: any) => {
        try{
            event.target.src = "../assets/UserSilhouette.png";
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at onImageNotFound`, error);
        }
    }
}
