import * as React from "react";
import { Logging } from "../logging/logging";
import { GetEnvConfig } from "../config/GetEnvConfig";
/**
 * State for the Dropdown React component
 */
export interface ICustumDropdownState {
    open?: boolean;
    dataSource: IDataSource;
}

/**
 * Properties for the Dropdown React component
 */
export interface ICustumDropdownProps {
    dataSource: IDataSource;
    keyID: string;
    onApplyClick(e: any, selectedData: IData[]): any;
}

export interface IDataSource {
    title: string;
    text: string;
    value: string;
    searchText: string;
    data: IData[];
}

export interface IData {
    text: string;
    value: string;
    selected: boolean;
}

/**
 * Implementation of the Dropdown component
 */
export class CustumDropdown extends React.Component<ICustumDropdownProps, ICustumDropdownState> {
    //declaration of class variable
    private container: any = null;
    //constructor to initiiaise the state variable.
    constructor(props: ICustumDropdownProps) {
        super(props);
        this.container = React.createRef();
        this.state = {
            open: false,
            dataSource: props.dataSource || { title: "", text: "", value: "", searchText: "", data: [] }
        };
    }

    //to set the state variable from the parent props
    public componentWillReceiveProps(nextProps: any) {
        this.setState({ dataSource: nextProps.dataSource });
    }

    //to register the mouse down event
    public componentDidMount() {
        document.addEventListener("mousedown", this.handleClickOutside);
    }

    //to un-register the mouse down even
    public componentWillUnmount() {
        document.removeEventListener("mousedown", this.handleClickOutside);
    }


    //to render the drop down list 
    public render() {

        const renderList = this.getRenderList();
        const title = this.getTitle();

        return (
            <div ref={this.container} className="dd-container">
                <div className="dd-box business_app"
                    style={{ display: "flex", justifyContent: "space-between" }}
                    onClick={(e) => this.onDDLClick(e)}>
                    <span className="wrapHeader">{title}</span>
                    <i className="ms-Dropdown-caretDown ms-Icon ms-Icon--ChevronDown arrowcss">
                    </i>
                </div>
                <div className="business_app_options">
                    <div className="chkbox-ul"
                        style={{ overflow: "hidden", display: (this.state.open ? "block" : "none") }}>
                        <div className="search-contr">
                            <i className="ms-Icon ms-Icon--Search x-hidden-focus" title="Search" aria-hidden="true"></i>
                            <input onChange={(e) => this.onSearchTextChange(e)} type="text"
                                aria-label="Search" value={this.state.dataSource.searchText}
                                className="AddSearchItem" placeholder="Search" />
                            <i className="ms-Icon ms-Icon--ChromeClose"
                                title="" aria-hidden="true"></i>
                        </div>
                        <ul>
                            {renderList}
                        </ul>
                        <div className="clear-apply" data-subscope="Documents" data-parent="IP Type">
                            <a onClick={(e) => this.onClearAllClick(e)} title="IP Type Clear" className="clearHorizontal" data-subscope="Documents" data-parent="IPType">
                                <i className="ms-Icon ms-Icon--ChromeClose" title="IP Type Clear" aria-hidden="true"></i>
                                <span>Clear</span>
                            </a>
                            <a onClick={(e) => this.onApplyClick(e)} className="applyHorizontal" title="IP Type Apply" data-subscope="Documents" data-parent="IPType">
                                <i className="ms-Icon ms-Icon--Accept" title="IP Type Apply" aria-hidden="true"></i>
                                <span>Apply</span>
                            </a>
                        </div>

                    </div>
                </div>
            </div>

        );
    }

    public handleClickOutside = event => {
        try {
            if (this.container.current && !this.container.current.contains(event.target)) {
                if (this.state.open) {
                    this.setState({
                        open: false,
                    });
                }
            }
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at handleClickOutside`, error);
        }
    }

    //to get the rende list 
    private getRenderList() {
        try {
            const filterList = this.state.dataSource && this.state.dataSource.data ?
                this.state.dataSource.searchText && this.state.dataSource.searchText.trim().length > 0
                    ? this.state.dataSource.data.filter(obj => obj.text.toLowerCase().includes(this.state.dataSource.searchText.toLowerCase()))
                    : this.state.dataSource.data : null;
            const returnList = filterList && [...filterList].map((item, i) => {
                return <li>
                    <div className="divCheckBoxFilter" >
                        <input className="styled-checkbox" id={this.props.keyID + i}
                            type="checkbox" title={item.text} checked={item.selected}
                            onClick={(e) => this.onChkClick(e, item)}
                            value={item.value} />
                        <label className="CheckBoxFilter WrapFilterText"
                            htmlFor={this.props.keyID + i} title={item.text}> {item.text} </label>
                        {item.selected ? <div className="testing" onClick={(e) => this.onChkClick(e, item)}></div> : null}
                    </div >
                </li>;
            });
            return returnList;
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at getRenderList`, error);
            return null;
        }
    }

    //to get the title
    private getTitle() {
        try {
            const filterList = this.state.dataSource && this.state.dataSource.data ?
                this.state.dataSource.searchText && this.state.dataSource.searchText.trim().length > 0
                    ? this.state.dataSource.data.filter(obj => obj.text.toLowerCase().includes(this.state.dataSource.searchText.toLowerCase()))
                    : this.state.dataSource.data : null;

            let title = this.state.dataSource.title;
            if (filterList && filterList.length > 0) {
                const selectData = filterList.filter(obj => obj.selected);
                if (selectData) {
                    title = "";
                    title = selectData.length > 0 ? selectData[selectData.length - 1].text : this.state.dataSource.title;
                    title = selectData.length > 1 ? title + " (+" + (selectData.length - 1) + ")" : title;
                }
            }
            return title;
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at getTitle`, error);
            return "";
        }
    }

    //to handle the dropdown click event    
    public onDDLClick(e: any) {
        this.setState({
            open: !this.state.open
        });
    }

    //to handle the check box click event
    public onChkClick(e: any, item: IData) {
        try {
            e.stopPropagation();
            e.preventDefault();
            const currObj = this.state.dataSource.data.find(obj => (obj.text.toLowerCase() === item.text.toLowerCase()
                && obj.value.toLowerCase() === item.value.toLowerCase()));
            if (currObj) {
                currObj.selected = !currObj.selected;
                this.setState({ dataSource: this.state.dataSource });
            }
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at onChkClick`, error);
        }
    }

    //to handle the clear all click event
    public onClearAllClick(e: any) {
        try {
            e.stopPropagation();
            e.preventDefault();
            this.state.dataSource.data.forEach(obj => (obj.selected = false));
            this.setState({ dataSource: this.state.dataSource });
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at onClearAllClick`, error);
        }
    }

    //to handle the apply click event
    public onApplyClick(e: any) {
        try {
            e.stopPropagation();
            e.preventDefault();
            const selectedData = this.state.dataSource.data.filter(obj => (obj.selected === true));
            this.setState({ open: false }, () => {
                this.props.onApplyClick(e, selectedData);
            });
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at onApplyClick`, error);
        }
    }

    //to handle the searchtext change event
    public onSearchTextChange(e: any) {
        try {
            this.state.dataSource.searchText = e.target.value;
            this.setState({ dataSource: this.state.dataSource });
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at onSearchTextChange`, error);
        }
    }
}
