import * as React from "react";
import { Provider, Flex, Header, Input } from "@fluentui/react-northstar";
import { useState, useEffect, useRef } from "react";
import { useTeams } from "msteams-react-base-component";
import * as microsoftTeams from "@microsoft/teams-js";
import { IDDLData, SSUXType } from "./config/appdata";
import { CustumDropdownSingleSelect, IDataSource, IData } from "./Components/CustumDropdownSingleSelect";

/**
 * Implementation of Find It configuration page
 */
export const FindItTabConfig = () => {

    const [{ inTeams, theme, context }] = useTeams({});
    const [text, setText] = useState<string>();
    const [defaultDomain, setDomain] = useState<string>(SSUXType.ipkit);
    const entityId = useRef("");


    const onSaveHandler = (saveEvent: microsoftTeams.settings.SaveEvent) => {
        const host = "https://" + window.location.host;
        microsoftTeams.settings.setSettings({
            contentUrl: host + "/findItTab/?name={loginHint}&tenant={tid}&group={groupId}&theme={theme}",
            websiteUrl: host + "/findItTab/?name={loginHint}&tenant={tid}&group={groupId}&theme={theme}",
            suggestedDisplayName: "FindIt",
            removeUrl: host + "/findItTab/remove.html?theme={theme}",
            entityId: entityId.current

        });
        saveEvent.notifySuccess();
    };

    const onCustumApplyClick = (e: any, selectedData: IData[], type: string) => {

        if (selectedData && selectedData.length > 0) {
            let entityType = SSUXType.offer;
            if (selectedData[0].text == "Offer")
                entityType = SSUXType.offer;
            else if (selectedData[0].text == "IPKits")
                entityType = SSUXType.ipkit;
            else if (selectedData[0].text == "Doc")
                entityType = SSUXType.docs;
            else if (selectedData[0].text == "Service Code")
                entityType = SSUXType.serviceCode;
            else if (selectedData[0].text == "Docs As Code")
                entityType = SSUXType.docascode;
            else if (selectedData[0].text == "SEE Teams")
                entityType = SSUXType.project;
            else if (selectedData[0].text == "Virtuoso")
                entityType = SSUXType.virtuoso;
            else if (selectedData[0].text == "CompassOne")
                entityType = SSUXType.compassOne;
            setDomain(entityType);
            entityId.current = entityType;
        }
    };

    useEffect(() => {
        if (context) {
            setText(context.entityId);
            entityId.current = context.entityId;
            setDomain(context.entityId);
            microsoftTeams.settings.registerOnSaveHandler(onSaveHandler);
            microsoftTeams.settings.setValidityState(true);
            microsoftTeams.appInitialization.notifySuccess();
        }
    }, [context]);



    return (
        <Provider theme={theme}>
            <Flex fill={true}>
                <Flex.Item>
                    <div>
                        <Header content="Configure your tab" />
                        <div style={{ display: "flex" }}>
                            <div style={{ padding: "5px" }}> Default Scope</div>
                            <CustumDropdownSingleSelect
                                onApplyClick={(e, selectedData) => onCustumApplyClick(e, selectedData, "domain")}
                                dataSource={{
                                    title: "Offer",
                                    text: "Offer",
                                    value: "Offer",
                                    searchText: "",
                                    data: [{
                                        text: "Offer",
                                        value: "Offer",
                                        selected: defaultDomain == SSUXType.offer
                                    }
                                        , {
                                        text: "IPKits",
                                        value: "IPKits",
                                        selected: defaultDomain == SSUXType.ipkit
                                    }, {
                                        text: "Doc",
                                        value: "Doc",
                                        selected: defaultDomain == SSUXType.docs
                                    }, {
                                        text: "Service Code",
                                        value: "Service Code",
                                        selected: defaultDomain == SSUXType.serviceCode
                                    }, {
                                        text: "Docs As Code",
                                        value: "Docs As Code",
                                        selected: defaultDomain == SSUXType.docascode
                                    }, {
                                        text: "SEE Teams",
                                        value: "SEE Teams",
                                        selected: defaultDomain == SSUXType.project
                                    }
                                        , {
                                        text: "Virtuoso",
                                        value: "Virtuoso",
                                        selected: defaultDomain == SSUXType.virtuoso
                                    }, {
                                        text: "CompassOne",
                                        value: "CompassOne",
                                        selected: defaultDomain == SSUXType.compassOne
                                    }]
                                }} keyID="DS" />
                        </div>
                    </div>
                </Flex.Item>
            </Flex>
        </Provider>
    );
};
