import * as React from "react";
import { Logging } from "../logging/logging";
import { GetEnvConfig } from "../config/GetEnvConfig";
import { Enter } from "../config/appdata";
/**
 * State for the SearchBox React component
 */
export interface ICustumSearchBoxSate {
    textData: string;
    showToolTip: boolean;
}

/**
 * Properties for the SearchBox React component
 */
export interface ICustumSearchBoxProps {
    textData: string;
    onSearchClick(e: any, searchText: string): any;
}
/**
 * Implementation of the SearchBox Component
 */
export class CustumSearchBox extends React.Component<ICustumSearchBoxProps, ICustumSearchBoxSate> {
    //to declare the class variable
    private searchContainer: any = null;

    //constructor to initiiaise the state variable.
    constructor(props: ICustumSearchBoxProps) {
        super(props);
        this.searchContainer = React.createRef();
        this.state = {
            textData: props.textData,
            showToolTip: false
        };
    }

    //to handle the out side click event
    public handleClickOutside = (event: any) => {
        if (this.searchContainer.current && !this.searchContainer.current.contains(event.target)) {
            if (this.state.showToolTip) {
                this.setState({
                    showToolTip: false,
                });
            }
        }
    }


    //to render the search box UI 
    public render() {
        return (
            <div ref={this.searchContainer} className="findItTopSearch" tabIndex={0}>
                <div className="mobiSearchInput">
                    <input aria-label="Search" type="text" placeholder="Search..." maxLength={2048}
                        accessKey="G" id="managedSearch" autoComplete="off" autoCorrect="off"
                        className="ms-srch-sb-prompt ms-helperText" value={this.state.textData} onChange={(e) => this.onSearchTextChange(e)}
                        onFocus={(e) => this.showToolTipClick()}
                        onKeyPress={(e) => this.handleKeyPress(e)}
                    />
                    <i id="ClearSearchText" className="ms-Icon ms-Icon--Clear"
                        onClick={(e) => this.onClearClick(e)} title="Clear" tabIndex={0}
                        onKeyPress={(e) => e.key === Enter ? this.onClearClick(e) : {}} style={{ display: (this.state.textData && this.state.textData.length > 0 ? "inline" : "none") }}></i>

                    <i onClick={(e) => this.onSearchClick(e)} className="ms-Icon ms-Icon--Search"
                        title="Search" aria-hidden="true" tabIndex={0}
                        onKeyPress={(e) => e.key === Enter ? this.handleKeyPress(e) : {}} ></i>
                    <div className="ms-qSuggest-container ms-shadow" id="AutoCompContainer">
                        <div id="ctl00_SmallSearchInputBox1_csr_AutoCompList"></div>
                    </div>
                </div>
            </div>
        );
    }

    //to show the tool tip
    public showToolTipClick() {
        if (!this.state.showToolTip) {
            this.setState({ showToolTip: true });
        }
    }
    //to set the state variable from the parent props
    public componentWillReceiveProps(nextProps: any) {
        if (this.state.textData != nextProps.textData)
            this.setState({ textData: nextProps.textData });
    }
    //to register the mouse click event
    public componentDidMount() {
        document.addEventListener("mousedown", this.handleClickOutside);
    }

    //to un-register the mouse click event
    public componentWillUnmount() {
        document.removeEventListener("mousedown", this.handleClickOutside);
    }

    //to handle the key press event 
    public handleKeyPress = (e: any) => {
        if (e.key === Enter) {
            this.onSearchClick(e);
        }
    }

    //to handle the search text change event
    public onSearchTextChange(e: any) {
        e.stopPropagation();
        e.preventDefault();
        this.setState({ textData: e.target.value });
    }

    //to handle the clear click event
    public onClearClick(e: any) {
        e.stopPropagation();
        e.preventDefault();
        this.setState({ textData: "" });
        Logging.AppInsightsTrackEvent(GetEnvConfig.AppSettings.appName, "Information", "Clear Searchbar", `Click to Clear Searchbar`, "");
    }

    //to handle the search click event
    public onSearchClick(e: any) {
        e.stopPropagation();
        e.preventDefault();
        this.props.onSearchClick(e, this.state.textData);
    }
}
