    public getUserProfile = async (userEmails: string[], memberRoles: Object, token: string): Promise<IUserData[]> => {
        return new Promise(async (resolve, reject) => {
            const myReturnList: IUserData[] = [];
            try {
                if (userEmails && userEmails.length > 0) {
                    const headers = new Headers({ "Accept": "application/json; odata=verbose", "authorization": `Bearer ${token}` });
                    const options = {
                        headers,
                        crossDomain: true
                    };
                    const maxBatchSize = 30;
                    for (let i = 0; i < userEmails.length; i += maxBatchSize) {
                        const queryText = userEmails.slice(i, i + maxBatchSize).join(" OR ");

                        let azureUrl = "https://microsoft.sharepoint.com/sites/campus/_api/search/query";
                        azureUrl += "?querytext=" + `'${queryText}'`
                        azureUrl += "&sourceid='B09A7990-05EA-4AF9-81EF-EDFAB16C4E31'" +
                            "&processpersonalfavorites=false&clienttype='Campus'&rowlimit=100" + "&selectproperties='PreferredName,JobTitle,PictureUrl,Path,WorkEmail,AccountName'";

                        const response = await fetch(azureUrl, options);
                        const body = await response.json();

                        if (body && body.d.query.PrimaryQueryResult != null && body.d.query.PrimaryQueryResult.RelevantResults != null) {
                            const data = body.d.query.PrimaryQueryResult.RelevantResults.Table.Rows.results;
                            data.forEach(async (result, index) => {

                                var jobTitle = result.Cells.results[3].Value == null ? "" : result.Cells.results[3].Value;
                                var name = result.Cells.results[2].Value;
                                var email = result.Cells.results[6].Value;

                                var alias = result.Cells.results[7].Value;
                                alias = alias.substring(alias.lastIndexOf('|') + 1, alias.indexOf('@'));

                                var acctName = result.Cells.results[5].Value;
                                let Memrole = this.getMemberRole(memberRoles, email, alias);

                                let MemberDomain = "";
                                var userProfileImg = result.Cells.results[4].Value;

                                myReturnList.push({ name: name, email: email, jobTitle: jobTitle, userImgUrl: userProfileImg, role: Memrole, domain: MemberDomain, acctName: acctName, profileUrl: "" });
                            })
                        }
                    }
                }
            }
            catch (error) {
                Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at getUserProfile", error);
            }
            resolve(myReturnList);
        })

    }


    public getMemberRole = (memberRoles: Object, email: string, alias: string): string => {
        let memberRole = "";
        try {
            for (let role in memberRoles) {
                memberRoles[role].forEach((emailID) => {
                    if (emailID !== "") {
                        if (emailID.toLowerCase() === email.toLowerCase() || emailID.toLowerCase() === alias.toLowerCase()) {
                            memberRole += role + ";";
                        }
                    }
                })
            }
        }
        catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at getMemberRole", error);
        }

        return memberRole;
    }


