import * as React from "react";
import { IOfferData } from "../config/appdata";
import { Logging } from "../logging/logging";
import { GetEnvConfig } from "../config/GetEnvConfig";
import { Icon } from "office-ui-fabric-react";
import { CommonService } from '../findItBL/commonservice';
import {  Enter } from "../config/appdata";


/**
 * State for the Tile View React component
 */
export interface ICustumTileSate {
    ShowAddToSEETeam: boolean;
    OfferResult?: IOfferData[];
    PageSize?: number;
    PageNo?: number;
    Index?: number;
}

/**
 * Properties for the Tile View React component
 */
export interface ICustumTileProps {
    offerResult?: IOfferData[];
    pageSize?: number;
    pageNo?: number;
    onAddToSEETeamClick(e: any, selectedData: IOfferData, showView: boolean, type: string): any;
}

/**
 * Implementation of the Tile View React Component
 */
export class CustumTileViewIpkit extends React.Component<ICustumTileProps, ICustumTileSate> {
    //to declare the class variable
    private tileContainer: any = null;
    private fileName: string = "CustumTileViewIpkit.tsx";
    private commonService: CommonService;
    //constructor to initiiaise the state variable.    
    constructor(props: ICustumTileProps) {
        super(props);
        this.tileContainer = React.createRef();
        this.state = {
            ShowAddToSEETeam: false
            , OfferResult: this.props.offerResult
            , PageNo: this.props.pageNo
            , PageSize: this.props.pageSize
            , Index: -1
        };
        this.commonService = new CommonService();
    }

    //to set the state variable from the parent props
    public componentWillReceiveProps(nextProps: any) {
        this.setState({
            OfferResult: nextProps.offerResult, PageSize: nextProps.pageSize
            , PageNo: nextProps.pageNo
        });
    }

    //to register the mouse click event 
    public componentDidMount() {
        document.addEventListener("mousedown", this.handleClickOutside);
    }

    //to un-register the mouse click event 
    public componentWillUnmount() {
        document.removeEventListener("mousedown", this.handleClickOutside);
    }


    //to render the Tile View UI
    public render() {
        let currOfferResult = this.state.OfferResult === undefined ? [] : this.state.OfferResult;
        const pageSize = this.state.PageSize === undefined ? 10 : this.state.PageSize;
        const pageNo = this.state.PageNo === undefined ? 1 : this.state.PageNo;
        currOfferResult = currOfferResult.filter(obj => obj.SrNo >= ((pageSize * (pageNo - 1) + 1))
            && obj.SrNo <= (pageSize * pageNo));
        let tileViewUI: any = null;
        if (currOfferResult && currOfferResult.length > 0) {
            tileViewUI = currOfferResult.map((item, index) => {

                item.Description = item.Description || "No Description"

                return <div className="ipkitTileView" style={{ display: "block" }}>
                    <div className="tile-contr ipkit_tiles">
                        <div className="tile-header">
                            <div tabIndex={0}
                                onKeyPress={(e) => e.key === Enter ? this.onShowAddToSEETeamClick(e, index) : {}}
                                className="offerheadergear" role="button"
                                aria-label="Add to SEE Teams popup" aria-expanded="false" aria-haspopup="listbox">
                                <i title="Open Menu" className="ms-Icon ms-Icon--AddTo"
                                    onClick={(e) => this.onShowAddToSEETeamClick(e, index)}>
                                </i>

                                <div tabIndex={0}
                                    onKeyPress={(e) => e.key === Enter ? this.onShowAddToSEETeamMenuClick(e, index, item) : {}}
                                    className="CampusProjectDetailsforoffers" title="Add to SEE Team(s)"
                                    style={{
                                        width: "183px !important"
                                        , display: (this.state.ShowAddToSEETeam === true && this.state.Index === index ? "block" : "none")

                                    }}
                                    unselectable="on">
                                    <ul role="listbox">
                                        <li onClick={(e) => this.onShowAddToSEETeamMenuClick(e, index, item)} role="option" tabIndex={0} className="Favouritedg">

                                            <span>
                                                <i className="ms-Icon ms-Icon--Add x-hidden-focus"></i>
                                            </span>Add to SEE Team(s)</li>
                                    </ul>
                                </div>
                            </div>
                        </div>

                        <div className="tileInner">
                            <a title={item.Title + '\n' + item.Description} href={item.URL}
                                tabIndex={0}
                                onKeyPress={(e) => e.key === Enter ? window.open(item.URL) : {}}
                                target="_blank" rel="noreferrer" className="outline OfferFocus ssOfferTitleValueClassName" >
                                <h2 className="truncate-2lines">{item.Title}</h2>
                                <p className="truncate-3lines">{item.Description}</p>
                            </a>

                            <div className="ipkitTileDocuments">
                                {item.Docs && (item.Docs.length > 1 || (item.Docs.length == 1 && item.Docs[0].OID > 0)) ?
                                    <ul className="rclp-links ipkitDocDetails">
                                        {
                                            item.Docs && item.Docs.length > 0 ?
                                                item.Docs.map(o => {
                                                    return o.OID > 0 ?
                                                        <li title="sas">
                                                            <a className="ssOfferTileDocumentsClassName" title={o.Title} href={o.DocURL}
                                                                tabIndex={0}
                                                                onKeyPress={(e) => e.key === Enter ? window.open(o.DocURL) : {}}

                                                                rel="noreferrer" target="_blank" >
                                                                <img alt="PowerPoint document" src={`../assets/${this.commonService.getFileTypeIcon(o.DocURL)}`} />
                                                            </a></li> : null;
                                                })
                                                : null
                                        }
                                    </ul> :
                                    <p className="truncate-3lines nodataClass"> No Documents </p>
                                }
                            </div>
                            <div className="NewtileBottom">
                                <span>
                                    {(item.relatedOfferInfo != undefined
                                        && item.relatedOfferInfo != null
                                        && item.relatedOfferInfo.length > 0 && item.OID > 0
                                        && item.relatedOfferInfo[0].length > 0)
                                        ? <div className="relatedOFfers">
                                            <a className="showElement" target="_blank"
                                                id="SS-IPKit-RelatedOffers-3"
                                                href={GetEnvConfig.AppSettings.relatedOfferRedirectURL + item.relatedOfferInfo[0]}>
                                                Related Offers</a></div>
                                        : null
                                    }
                                </span>
                                <span>{item.TotalViews && item.TotalViews > 0 ? item.TotalViews : 0} Views</span>
                                {
                                    item.CodeLink && item.CodeLink.trim().length > 0 ? <span style={{ textAlign: 'left' }}>
                                        <a className="showElement" target="_blank" href={item.CodeLink}> Code </a>
                                    </span> : <></>
                                }
                                <span style={{ textAlign: "right" }}>{item.Downloads && item.Downloads > 0 ? item.Downloads : 0} <Icon iconName="Download" /></span>
                            </div>
                        </div>
                    </div>
                </div>

            });
        }
        return (
            <>
                {tileViewUI}
            </>
        );
    }


    //to handle the click out side event 
    public handleClickOutside = (event: any) => {
        try {
            if (this.tileContainer.current && !this.tileContainer.current.contains(event.target)) {
                if (this.state.ShowAddToSEETeam) {
                    this.setState({
                        ShowAddToSEETeam: false,
                    });
                }
            }
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at handleClickOutside() in CustumTileViewIpkit.tsx" + this.fileName, error);
        }
    }

    //to show the Add to SEE Team popup 
    public onShowAddToSEETeamClick(e: any, currIndex: number) {
        try {
            this.setState({
                ShowAddToSEETeam: true,
                Index: currIndex
            });
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onShowAddToSEETeamClick() in CustumTileViewIpkit.tsx" + this.fileName, error);
        }
    }

    //to show the Add to SEE Team Menu click 
    public onShowAddToSEETeamMenuClick(e: any, index: number, item: IOfferData) {
        try {
            e.stopPropagation();
            e.preventDefault();
            this.setState({
                ShowAddToSEETeam: false,
                Index: -1
            });
            this.props.onAddToSEETeamClick(e, item, true, "ipkit");
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onShowAddToSEETeamMenuClick() in CustumTileViewIpkit.tsx" + this.fileName, error);
        }
    }
}
