export interface IFindItAppData {
    offerData: ISSUXData;
    ipkitData: ISSUXData;
    compassOneData: ISSUXData;
    docsData: ISSUXData;
    virtuosoData: ISSUXData;
    docAsCodeData: ISSUXData;
    projectData: ISSUXData;
    serviceCodeData: ISSUXData;
}

export enum SSUXType {
    offer = "offer",
    ipkit = "ipkit",
    docs = "docs",
    compassOne = "compassOne",
    virtuoso = "virtuoso",
    docascode = "docascode",
    project = "project",
    serviceCode = "servicesCode"
}

export const Enter = "Enter";

export interface ISSUXData {
    count: number
    value: IOfferData[];
    MSProduct?: IDDLData[];
    ServicesDomain?: IDDLData[];
    Organisation?: string;
    Project?: IDDLData[];
}

export interface IOfferData {
    Description: string;
    FileCount: number;
    Title: string;
    SrNo: number;
    OID: number;
    Docs: IDocument[];
    IPType: string;
    LastModified: string;
    TotalViews: number;
    Rating: number;
    URL: string;
    Downloads?: number;
    CodeLink?: string;
    Domains: string[];
    CustomerNames: string[];
    Location: string;
    UserData: IUserData[];
    UserEmails: string[];
    MemberRoles: Object;
    OpportunityID: string;
    SalesStage: string;
    ProjectName: string;
    Organisation: string;
    Content?: string;
    relatedOfferInfo?: string[];
}

export interface IUserData {
    jobTitle: string;
    email: string;
    userImgUrl: string;
    name: string;
    role: string;
    domain: string;
    acctName: string;
    profileUrl: string;
}

export interface IDDLData {
    value: string;
    text: string;
    count: number;
    selected: boolean;
}

export interface IFilterData {
    MSProduct: string[]
    , ServiceDomain: string[]
}

export interface IOfferDocument {
    OID: number[]
    , AssetUrl: string
    Title: string
}

export interface IDocument {
    SrNo: number
    , DocURL: string
    , Title: string
    , OID: number,
    lastModifed?: string,
    views?:number,
    profileImg? : string;
    jobTitle?: string;
    email?:string;
}



