import * as React from "react";
import { Logging } from "../logging/logging";
import { GetEnvConfig } from "../config/GetEnvConfig";
import {  Enter } from "../config/appdata";

/**
 * State for the Pagination React component
 */
export interface ICustumPagingState {
    currPage: number;
    showRecPerPage: boolean;
    recPerPage: number;
    noOfRecords: number;
}

/**
 * Properties for the Pagination React component
 */
export interface ICustumPagingProps {
    noOfRecords: number;
    pageNo?: number;
    onPagingChange(e: any, pageSize: number, pageNo: number): any;
}

/**
 * Implementation of the Pagination Tab content page
 */
export class CustumPaging extends React.Component<ICustumPagingProps, ICustumPagingState> {
    //to declare the class variable
    private container: any = null;
    private noOfBox: number = 5;

    //constructor to initiiaise the state variable.
    constructor(props: ICustumPagingProps) {
        super(props);
        this.container = React.createRef();
        this.state = {
            currPage: 1,
            showRecPerPage: false,
            recPerPage: 10,
            noOfRecords: props.noOfRecords
        };
    }

    //to set the state variable from the parent props
    public componentWillReceiveProps(nextProps: any) {
        this.setState({ noOfRecords: nextProps.noOfRecords, currPage: nextProps.pageNo });
    }

    //to  register the mouse click event
    public componentDidMount() {
        document.addEventListener("mousedown", this.handleClickOutside);
    }
    //to  un-register the mouse click event
    public componentWillUnmount() {
        document.removeEventListener("mousedown", this.handleClickOutside);
    }

    //to render the pagination control UI
    public render() {
        const numUI = this.getUI();
        return (
            <div className="ssuxParentPagination">
                <div className="pagination Pagination-show-hideOffers">
                    <div className="sspaginationParent">
                        <div className="sspaginationcount" style={{ display: "flex" }}>
                            {numUI}
                        </div>
                        <div className="dropdown" tabIndex={0}
                            onKeyPress={(e) => this.toggleRecPerPage(e)}
                            onClick={(e) => this.toggleRecPerPage(e)}
                            aria-label="Items per page 10 items button list with 4 items" role="button"
                            aria-expanded="false" aria-haspopup="listbox" title="10">
                            <div className="dropdown-toggle dropdowndiv-Ipkits" id="dropdown-toggle-Offers" data-toggle="dropdown">{this.state.recPerPage}<i className="ms-Icon ms-Icon--CaretSolidDown newPaginationddIcon" aria-hidden="true"></i></div>
                            <ul className="dropdown-menu dropdownul-Offers dropdownMenuClose"
                                id="dropdown-menuOffers" role="listbox"
                                aria-label="Pagination Dropdown"
                                style={{ display: (this.state.showRecPerPage ? "block" : "none") }}>
                                <li style={{ pointerEvents: "none" }}><span className="paginationDropdownBeak"></span></li>
                                <h4 role="presentation" className="dropdown-header">Items per page</h4>
                                <li tabIndex={0} onKeyPress={(e) => e.key === Enter ? this.onPerPageClick(e, 10) : {}}
                                    role="option" onClick={(e) => this.onPerPageClick(e, 10)}>
                                    <p style={{ fontSize: "14px" }}>10
                                        <span className="paginationCheckMark">
                                            {
                                                this.state.recPerPage === 10 ?
                                                    <i title="" className="ms-Icon ms-Icon--CheckMark"></i>
                                                    : null
                                            }
                                        </span>
                                    </p>
                                </li>
                                <li role="option" tabIndex={0} onKeyPress={(e) => e.key === Enter ? this.onPerPageClick(e, 20) : {}}
                                    onClick={(e) => this.onPerPageClick(e, 20)}>
                                    <p style={{ fontSize: "14px" }}>20
                                        <span className="paginationCheckMark">
                                            {
                                                this.state.recPerPage === 20 ?
                                                    <i title="" className="ms-Icon ms-Icon--CheckMark"></i>
                                                    : null
                                            }
                                        </span>
                                    </p>
                                </li>
                                <li role="option" tabIndex={0} onKeyPress={(e) => e.key === Enter ? this.onPerPageClick(e, 50) : {}}
                                    onClick={(e) => this.onPerPageClick(e, 50)}>
                                    <p style={{ fontSize: "14px" }}>50
                                        <span className="paginationCheckMark">
                                            {
                                                this.state.recPerPage === 50 ?
                                                    <i title="" className="ms-Icon ms-Icon--CheckMark"></i>
                                                    : null
                                            }
                                        </span>
                                    </p>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    //to get the UI for render
    private getUI() {
        let numUI: any = null;
        let j = this.state.currPage / this.noOfBox;
        if (parseInt(j.toString(), 10) < this.state.currPage / this.noOfBox) {
            j++;
        }

        j = parseInt(j.toString(), 10);
        const start = (((j - 1) * this.noOfBox) + 1);
        const end = j * 5;
        const total = this.state.noOfRecords;
        const pageSize = this.state.recPerPage;
        const dslenght = this.state.noOfRecords;

        // if (revenueTableDataF && revenueTableDataF.length > 0) {
        if (dslenght > 0) {
            const lastrecord = total / pageSize;
            let isLastReached = false;
            if (dslenght > pageSize) {
                let numList;
                const array: any[] = [];
                for (let s = start; s <= end; s++) {
                    if (s <= Math.ceil(lastrecord)) {
                        array.push(s);
                    } else {
                        isLastReached = true;
                    }
                }
                numList = array.map((obj, i) => {
                    let classNames = "";
                    if (obj === this.state.currPage) {
                        classNames = "page active";
                    } else {
                        classNames = "page";
                    }
                    return <li className={classNames}><a href="#" className="page-link"
                        onKeyPress={(e) => e.key === Enter ? this.goToPage(e, obj) : {}}
                        onClick={(e) => this.goToPage(e, obj)} aria-label="page1current page">{obj}</a></li>;

                });

                const totalend = end + 1;
                numList = <ul className="ss-pagination-counterOffers pagination" style={{ marginTop: "5px" }}>
                    {numList}
                </ul>;

                numUI = <div className="links" style={{ display: "flex", minWidth: "130px", justifyContent: "space-between" }}>

                    {
                        this.state.currPage === 1 ?
                            <i className="ms-Icon ms-Icon--ChevronLeftMed ssuxPagination--paginationDocuments prev disabled" title="Previous page" aria-hidden="true"
                                style={{ pointerEvents: "none" }}></i>
                            :
                            <i className="ms-Icon ms-Icon--ChevronLeftMed ssuxPagination--paginationDocuments prev"
                                tabIndex={0} onKeyPress={(e) => e.key === Enter ? this.goToPrevious(e) : {}}
                                onClick={(e) => this.goToPrevious(e)} title="Previous page" aria-hidden="true" ></i>

                    }
                    {numList}
                    {
                        Math.ceil(lastrecord) === this.state.currPage ?
                            <i className="ms-Icon ms-Icon--ChevronRightMed ssuxPagination--paginationDocuments next disabled"
                                style={{ pointerEvents: "none" }} title="Next page" aria-hidden="true"></i>
                            :
                            <i className="ms-Icon ms-Icon--ChevronRightMed ssuxPagination--paginationDocuments next"
                                tabIndex={0} onKeyPress={(e) => e.key === Enter ? this.goToNext(e) : {}}
                                onClick={(e) => this.goToNext(e)} title="Next page" aria-hidden="true" ></i>

                    }
                </div>;
            }
        }
        return numUI;
    }

    //to handle the click out side event
    public handleClickOutside = (event: any) => {
        if (this.container.current && !this.container.current.contains(event.target)) {

        }
    }

    //to handle the toggle per page event
    public toggleRecPerPage(e: any) {
        e.stopPropagation();
        this.setState({ showRecPerPage: !this.state.showRecPerPage });
    }

    //to handle the per page click event
    public onPerPageClick(e: any, noOfRec: number) {
        try {
            e.stopPropagation();
            const pageNo = 1;
            this.setState({ showRecPerPage: false, recPerPage: noOfRec, currPage: pageNo });
            this.props.onPagingChange(e, noOfRec, pageNo);
            Logging.AppInsightsTrackEvent(GetEnvConfig.AppSettings.appName, "Information", "Changed Record count per page", ` CHnaged record count to ${noOfRec}`, "");
        }
        catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onPerPageClick() in CustumPaging.tsx", error);
        }
    }

    //to handle the go to page event
    public goToPage(e: any, pageNo: number) {
        try {
            e.stopPropagation();
            this.setState({ currPage: pageNo });
            this.props.onPagingChange(e, this.state.recPerPage, pageNo);
            Logging.AppInsightsTrackEvent(GetEnvConfig.AppSettings.appName, "Information", "Change Page", `Clicked on page ${pageNo}`, "");
        }
        catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at goToPage() in CustumPaging.tsx", error);
        }
    }

    //to handle the previous click event
    public goToPrevious(e: any) {
        try {
            e.stopPropagation();
            let pageNo = this.state.currPage;
            pageNo = pageNo - 1;
            pageNo = pageNo < 1 ? 1 : pageNo;
            this.setState({ currPage: pageNo });
            this.props.onPagingChange(e, this.state.recPerPage, pageNo);
            Logging.AppInsightsTrackEvent(GetEnvConfig.AppSettings.appName, "Information", "Go to previous page", `Go to page:${pageNo}`, "");
        }
        catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at goToPrevious() in CustumPaging.tsx", error);
        }
    }

    //to handle the next click event
    public goToNext(e: any) {
        try {
            e.stopPropagation();
            let pageNo = this.state.currPage;
            pageNo = pageNo + 1;
            this.setState({ currPage: pageNo });
            this.props.onPagingChange(e, this.state.recPerPage, pageNo);
            Logging.AppInsightsTrackEvent(GetEnvConfig.AppSettings.appName, "Information", "Go to next page", `Go to page: ${pageNo}`, "");
        }
        catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at goToNext() in CustumPaging.tsx", error);
        }
    }

}
