import * as React from "react";
import { Logging } from "../logging/logging";
import { GetEnvConfig } from "../config/GetEnvConfig";
import {  Enter } from "../config/appdata";
/**
 * State for the Tag Bar React component
 */
export interface ICustumTagBarSate {
    tagBarItems: string[];
    showTagBar: boolean;
}

/**
 * Properties for the Tag Bar React component
 */
export interface ICustumTagBarProps {
    tagBarItems: string[];
    showTagBar: boolean;
    onToggleTagBarClick(e: any, status: boolean): any;
    onRemoveTagItems(e: any, isRemoveAll: boolean, tagBarItem: string): any;
}
/**
 * Implementation of the Tag Bar UI
 */
export class CustumTagBar extends React.Component<ICustumTagBarProps, ICustumTagBarSate> {
    private fileName: string = "CustumTagBar.tsx";
    //constructor to initiiaise the state variable.    
    constructor(props: ICustumTagBarProps) {
        super(props);
        this.state = {
            tagBarItems: props.tagBarItems
            , showTagBar: props.showTagBar
        };
    }

    //to set the state variable from the parent props
    public componentWillReceiveProps(nextProps: any) {
        this.setState({ showTagBar: nextProps.showTagBar, tagBarItems: nextProps.tagBarItems });
    }

    //to render the Tag Bar UI 
    public render() {
        const myDispList = this.getTagBarItem();
        return (<div className="srp-tagitems">
            <span className="tag-title" title="Filters">Filters</span>
            <ul>

                {myDispList}

                {this.state.tagBarItems && this.state.tagBarItems.length > 0 ?
                    <li tabIndex={0}
                        onKeyPress={(e) => e.key === Enter ? this.onClearAllClick(e) : {}}
                        onClick={(e) => this.onClearAllClick(e)} className="srp-tagitem outline" id="SS-TagBar-RemoveAll" aria-label="Tag" >
                        <span className="srp-tagitemname" title="Clear All">Clear</span>
                        <span className="srp-removetagitem outline" title="Clear All" aria-hidden="true">
                            <i className="ms-Icon ms-Icon--ChromeClose x-hidden-focus"></i></span></li>
                    : null}
            </ul>
        </div>
        );
    }

    //to get the tag bar item
    private getTagBarItem() {
        try {
            const myDispList = this.state.tagBarItems.map((item, index) => {
                return <li className="srp-tagitem"
                    role="button" title={item} aria-label={`${item} filter remove tag`} tabIndex={0}
                    value={item}
                    onKeyPress={(e) => e.key === Enter ? this.onRemoveItemClick(e, item) : {}}
                >
                    <span className="srp-tagitemname  outline" aria-hidden="true">{item}</span>
                    <a data-subscope="Con
tentAll" className="srp-removetagitem" title="Remove Tag" aria-hidden="true" onClick={(e) => this.onRemoveItemClick(e, item)}>
                        <i className="ms-Icon ms-Icon--ChromeClose x-hidden-focus"></i></a></li>;
            });
            return myDispList;
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at getTagBarItem() in " + this.fileName, error);
        }
    }

    //to clear click all event 
    public onClearAllClick(e: any) {
        try {
            e.stopPropagation();
            e.preventDefault();
            this.setState({ tagBarItems: [] }, () => {
                this.props.onRemoveTagItems(e, true, "");
            });
            Logging.AppInsightsTrackEvent(GetEnvConfig.AppSettings.appName, "Information", "Clear all filter tags", `Clicked on Clear All in Tagbar`, "");
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onClearAllClick() in " + this.fileName, error);
        }
    }

    //to remove item click 
    public onRemoveItemClick(e: any, item: string) {
        try {
            e.stopPropagation();
            e.preventDefault();
            let myCurrData = [...this.state.tagBarItems];
            myCurrData = myCurrData.filter(obj => obj !== item);
            this.setState({ tagBarItems: myCurrData }, () => {
                this.props.onRemoveTagItems(e, false, item.toLowerCase());
            });
            Logging.AppInsightsTrackEvent(GetEnvConfig.AppSettings.appName, "Information", "Remove Filter tag", `Remove filter tag: ${item}`, "");
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onRemoveItemClick() in " + this.fileName, error);
        }
    }

    //to close the tag bar 
    public onCloseTagBar(e: any) {
        try {
            e.stopPropagation();
            e.preventDefault();
            this.setState({ showTagBar: false }, () => { this.props.onToggleTagBarClick(e, this.state.showTagBar); });
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onCloseTagBar() in " + this.fileName, error);
        }
    }
}
