import { JoinOnlineMeetingIcon } from "@fluentui/react-icons";
import * as React from "react";
import { AddToSEETeams, AddToSEETeamsRef, JoinedTeams, AddToSEETeamsChannel } from "../config/appdata";
import { Logging } from "../logging/logging";
import { GetEnvConfig } from "../config/GetEnvConfig";

/**
 * State for the Add to SEE Teams React component
 */
export interface ICustumAddToSEETeamSate {
    AddToSEETeamsRef: AddToSEETeamsRef;
    JoinedTeams: JoinedTeams[];
    fiterTeams: string;
}

/**
 * Properties for the  Add to SEE Teams React component
 */
export interface ICustumAddToSEETeamProps {
    showAddToSEETeams: boolean;
    onCloseSEETeams(e: any, status: boolean): any;
    onSaveAddToSEETeams(e: any, oid: number, title?: string, projectSiteIDs?: string[]): any;
    addToSEETeamsRef: AddToSEETeamsRef
    joinedTeams: JoinedTeams[];
    isTeamCreated?: boolean;
    isTeamLoading?: boolean;
    isSavingSEETeams?: boolean;
}

/**
 * Implementation of the Add to SEE Teams component
 */
export class CustumAddToSEETeam extends React.Component<ICustumAddToSEETeamProps, ICustumAddToSEETeamSate> {

    //constuctor to be used for initialising the state variable
    constructor(props: ICustumAddToSEETeamProps) {
        super(props);
        this.state = {
            AddToSEETeamsRef: props.addToSEETeamsRef
            , JoinedTeams: props.joinedTeams
            , fiterTeams: ""
        };
    }
    //to set the state variable from the parent props
    public componentWillReceiveProps(nextProps: any) {
        this.setState({
            AddToSEETeamsRef: nextProps.addToSEETeamsRef,
            JoinedTeams: nextProps.joinedTeams
        })
    }

    /**
     To render the UI
     */
    public render() {
        let seeTeamsObj: AddToSEETeams[] = [];
        let addToSEETeamsObj: AddToSEETeamsRef = { OID: this.state.AddToSEETeamsRef.OID }
        addToSEETeamsObj.AddToSEETeamsData = seeTeamsObj;
        let isAnySelected: boolean = false;
        if (this.state.AddToSEETeamsRef && this.state.AddToSEETeamsRef.AddToSEETeamsData && this.state.AddToSEETeamsRef.AddToSEETeamsData.length > 0) {
            let teamsIds = this.state.JoinedTeams.map(function (m) { return m.ID });
            let recentProjectList: AddToSEETeams[] = this.state.AddToSEETeamsRef.AddToSEETeamsData.filter((Str) => { return (teamsIds.indexOf((Str.GroupId ? Str.GroupId : "")) != -1) });
            if (recentProjectList && recentProjectList.length > 0 && this.state.fiterTeams && this.state.fiterTeams.length > 0)
                recentProjectList = recentProjectList.filter(o => o.Title.toLowerCase().includes(this.state.fiterTeams.toLowerCase()))
            addToSEETeamsObj.AddToSEETeamsData = recentProjectList && recentProjectList.length > 0 ? recentProjectList : [];
        }
        return (<>
            <div style={{ display: (this.props.showAddToSEETeams === true ? "block" : "none") }}
                className="ms-Panel ms-Panel--md knowlege-Map-Panel is-open kmAddRightPane">
                <div className="ms-Panel-contentInner">
                    <div className="kmPanelHeader">
                        <p className="ms-Panel-headerText">
                            <h1>Add to SEE Team(s)</h1></p><span className="kmContextualHelpIcon"
                                id="KM-AddRightpanelContextualHelp"></span>
                        <button style={{ top: "10px" }} className="ms-Panel-closeButton ms-PanelAction-close closefocus" title="Close" id="KmCloseIconrightpanel">
                            <i onClick={(e) => this.closeAddToSEETeams(e)} className="ms-Panel-closeIcon ms-Icon ms-Icon--Cancel" title="Close"></i></button>
                    </div>
                    <div className="ms-Panel-content">
                        <div className="addKmRtpanelContainer">
                            <div className="addToKmheadertitle">
                                <p id="itemTitleID">Add
                                                        <b title="Adoption and Change Management Accelerator">&nbsp;{this.state.AddToSEETeamsRef ? this.state.AddToSEETeamsRef.Title : null}..</b> to one or more of your SEE Team(s).
                                                        Please specify which SEE Team(s) to add the item.</p>
                            </div>
                            <div className="kmProjectListContainer">
                                <h4>Select the SEE Team(s) channels</h4>
                                <div className="checkBoxDiv kmSelectedProject">
                                    <input type="checkbox" value="Contoso M&amp;T Team" />
                                    <label className="checkBoxFilter">Contoso M&amp;T Team</label>
                                </div>
                                <div className="kmSelectProjectContainer">
                                    <div className="KmProjectFilterDiv"></div>
                                    <div className="kmProjectSearchDiv"><i className="ms-Icon ms-Icon--Search"></i>
                                        <input type="text" title="Search" aria-label="SEE Teams channels search edit"
                                            placeholder="Search" className="AddItemSearch" id="BP-IPKit-Searchbox"
                                            value={this.state.fiterTeams} onChange={(e) => this.onFilterTextChange(e)} />
                                        <i id="kmClearSearchProjects" className="ms-Icon ms-Icon--Clear" title="Clear" aria-hidden="true"
                                            style={{ display: "none" }}></i></div>
                                    <div className="KmProjectListSelection">
                                        {
                                            addToSEETeamsObj && addToSEETeamsObj.AddToSEETeamsData
                                                && addToSEETeamsObj.AddToSEETeamsData.length > 0
                                                ?
                                                addToSEETeamsObj.AddToSEETeamsData.map((obj, index) => {
                                                    return <div className="addtoTeamChannelsSection">
                                                        <h3>{obj.Title}</h3>
                                                        {
                                                            obj.Channel && obj.Channel.length > 0 ?
                                                                <div className="choicefieldselect" id="choicefieldgroup_25262" role="radiogroup">
                                                                    <ul className="ms-ChoiceFieldGroup-list">
                                                                        {
                                                                            obj.Channel && obj.Channel.map((o, i) => {
                                                                                if (isAnySelected == false && o.IsAdded)
                                                                                    isAnySelected = o.IsAdded;

                                                                                if (obj.IsExist || (this.props.isTeamCreated != undefined && this.props.isTeamCreated))
                                                                                    return <li className="ms-RadioButton radioSeeButtom" title="The Selected item was already added to the SEE Team Channel">
                                                                                        <input tabIndex={-1} aria-hidden="true" name="contact" type="radio"
                                                                                            className="ms-RadioButton-input kmprojectsitelistcheck" id="radioinput"
                                                                                            style={{ marginTop: "-4px;" }} />
                                                                                        <label aria-disabled="true"
                                                                                            title="Pre-Sales" role="radio" aria-label="Pre-Sales" className="ms-RadioButton-field is-disabled"
                                                                                            tabIndex={-1} aria-checked="true" id="choicefieldgroup_25262_Pre-Sales"
                                                                                            style={{ opacity: "0.7" }}>
                                                                                            <span className="ms-Label ml5" aria-hidden="true">{o.Title}</span></label></li>
                                                                                else
                                                                                    return <li className="ms-RadioButton radioSeeButtom">
                                                                                        <input tabIndex={-1} aria-hidden="true" name="contact" type="radio"
                                                                                            className="ms-RadioButton-input kmprojectsitelistcheck" id="radioinput"
                                                                                            style={{ marginTop: "-4px;" }} />
                                                                                        <label aria-disabled="false"
                                                                                            onClick={(e) => this.channelSelected(e, o.IsAdded, o.ChannelName, obj.ID)}
                                                                                            title="Pre-Sales" role="radio" aria-label="Pre-Sales"
                                                                                            className={o.IsAdded ? "ms-RadioButton-field is-checked" : "ms-RadioButton-field"}
                                                                                            tabIndex={-1} aria-checked="true" id="choicefieldgroup_25262_Pre-Sales">
                                                                                            <span className="ms-Label ml5" aria-hidden="true">{o.Title}</span></label></li>



                                                                            })
                                                                        }

                                                                    </ul>
                                                                </div>

                                                                : null}
                                                    </div>
                                                })
                                                : <div className="kmprojectsitelist">
                                                    <div id="ProjectNameandDetails">You're not a Owner/Member of any SEE Teams</div>
                                                </div>
                                        }
                                    </div>
                                </div>
                            </div>
                            <div className="kmAddCancwlBtnContainer">
                                {this.props.isTeamCreated == undefined || this.props.isTeamCreated == false ?
                                    <button title="Add to SEE Team(s)"
                                        onClick={(e) => this.onSaveAddToSEETeams(e)}
                                        className={isAnySelected ? "kmAddbutton" : "kmAddbutton disableAddToKmap addOffer"}
                                        id="AddToSeeTeamsButton" aria-disabled="true">Add to SEE Team(s)</button>
                                    : null}
                                <button className="AddtoSEETeamCancelButton"
                                    onClick={(e) => this.closeAddToSEETeams(e)}
                                    title={this.props.isTeamCreated == undefined || this.props.isTeamCreated == false ? "Cancel" : "Close"} id="AddToSeeTeamCancelButtonId">
                                    {this.props.isTeamCreated == undefined || this.props.isTeamCreated == false ? "Cancel" : "Close"}</button>
                            </div>
                            {this.props.isTeamCreated ? <div id="seeTeamsMessage">
                                <div className="msgContraddtoSee successMsg">
                                    Item added successfully to the SEE Team(s) channel.<br />
                                    <b>NOTE: Adding Item(s) to SEE Team(s) may take upto 24 hrs.
                                          </b>
                                </div></div> : null}



                        </div>
                    </div>
                    <div className="Loader_contr" style={{ display: (this.props.isTeamLoading ? "flex" : "none") }}>
                        <div className="Fabric_Loader"></div>
                        <p> Loading SEE Team(s)...</p>
                    </div>
                    <div className="Loader_contr" style={{ display: (this.props.isSavingSEETeams ? "flex" : "none") }}>
                        <div className="Fabric_Loader"></div>
                        <p> Adding Items to SEE Team(s)...</p>
                    </div>
                </div>
            </div>


        </>
        );
    }

    public channelSelected(e: any, isChecked?: boolean, channelName?: string, teamId?: string) {
        try {
            e.stopPropagation();
            e.preventDefault();
            let myAddToSEERefObj = this.state.AddToSEETeamsRef;
            let myCurrObj = myAddToSEERefObj.AddToSEETeamsData?.find(o => o.ID == teamId);
            if (myCurrObj && myCurrObj.Channel) {
                myCurrObj.Channel.forEach(o => { o.IsAdded = (o.ChannelName == channelName ? (isChecked == undefined ? true : !isChecked) : false) });
            }
            this.setState({ AddToSEETeamsRef: myAddToSEERefObj });
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at channelSelected()`, error);
        }
    }

    // to handle the filter change event
    public onFilterTextChange(e: any) {
        try {
            e.stopPropagation();
            e.preventDefault();
            this.setState({ fiterTeams: e.target.value });
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at onFilterTextChange`, error);
        }
    }

    // to close the Add to SEE Teams popup window
    public closeAddToSEETeams(e: any) {
        try {
            this.props.onCloseSEETeams(e, false);
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at closeAddToSEETeams()`, error);
        }
    }
    public onSaveAddToSEETeams(e: any) {
        try {
            let ProjectSiteIDs: string[] = [];
            let addToSEETeamsRef = this.state.AddToSEETeamsRef;
            if (addToSEETeamsRef && addToSEETeamsRef.AddToSEETeamsData && addToSEETeamsRef.AddToSEETeamsData.length > 0) {
                let oid = addToSEETeamsRef.OID;
                let title = addToSEETeamsRef.Title ? addToSEETeamsRef.Title : "";
                addToSEETeamsRef.AddToSEETeamsData.forEach(obj => {
                    if (obj.Channel && obj.Channel.length > 0) {
                        obj.Channel.forEach(o => {
                            if (o.IsAdded != undefined && o.IsAdded == true)
                                ProjectSiteIDs.push(obj.ProjectSiteID + "_" + o.ChannelName)
                        });

                    }
                })
                this.props.onSaveAddToSEETeams(e, oid, title, ProjectSiteIDs);
            }
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, `Exception occured at onSaveAddToSEETeams()`, error);
        }
    }

}
