import * as React from "react";
/**
 * State for the Filter Text React component
 */
export interface ICustumFilterTextSate {
    textData: string;
}

/**
 * Properties for the Filter Text React component
 */
export interface ICustumFilterTextProps {

}
/**
 * Implementation of the Filter Text React component
 */

export class CustumFilterText extends React.Component<ICustumFilterTextProps, ICustumFilterTextSate> {
    //constructor to initiiaise the state variable.
    constructor(props: ICustumFilterTextProps) {
        super(props);
        this.state = {
            textData: ""
        };
    }

    //to renturn the filter text control
    public render() {
        return (
            <div className="dd-box business_app" title="Keyword">
                <i className="ms-Icon ms-Icon--Filter x-hidden-focus filterInputHR"
                    style={{ cursor: "default", display: "block" }} title="Filter" aria-hidden="true"></i>
                <label htmlFor="Filter by Keyword..." style={{ display: "none" }}></label>
                <input type="text"
                    placeholder="Filter by Keyword..." aria-label="KeywordSearch"
                    maxLength={2048} accessKey="S" autoComplete="off"
                    id="filterInput" value={this.state.textData} onChange={(e) => this.onSearchTextChange(e)} />
                <i className="ms-Icon ms-Icon--Clear"
                    onClick={(e) => this.onClearClick(e)} title="Clear" aria-hidden="true" style={{ display: (this.state.textData.length > 0 ? "block" : "none") }}></i>
            </div>
        );
    }

    //to handle the search text change event
    public onSearchTextChange(e: any) {
        this.setState({ textData: e.target.value });
    }

    //to handle the clear click event
    public onClearClick(e: any) {
        this.setState({ textData: "" });
    }
}
