import * as Msal from "msal";
import * as microsoftTeams from "@microsoft/teams-js";


export class AuthService {
    private app: Msal.UserAgentApplication;
    private applicationConfig: any;
    constructor() {
        const redirectUri1 = window.location.href;
        this.applicationConfig = {
            clientID: "e0f60ff0-327b-43e2-9055-54782bbed141",
            teamsAPI: ["User.Read"],
            campusApI: ["https://campuswebapi.azurewebsites.net/user_impersonation"]
        };
        this.app = new Msal.UserAgentApplication(
            {
                auth: {
                    clientId: this.applicationConfig.clientID,
                    redirectUri: redirectUri1,
                    postLogoutRedirectUri: window.location.origin,
                    authority: "https://login.microsoftonline.com/72f988bf-86f1-41af-91ab-2d7cd011db47/"
                },
                system: {
                    loadFrameTimeout: 10000
                },
                cache: {
                    cacheLocation: "localStorage",
                    storeAuthStateInCookie: true
                }
            }
        );
    }

    public getAuthenticationParameters(type: string): any {
        const user = this.app.getAccount();
        let authenticationParameters: any;
        let scope = ["https://campuswebapi.azurewebsites.net/user_impersonation"];
        if (type === "graph")
            scope = ["https://graph.microsoft.com/User.Read"]
        else if (type === "site")
            scope = ["https://microsoft.sharepoint.com/AllSites.Read"]
        else if (type === "devops")
            scope = ["499b84ac-1321-427f-aa17-267ca6975798/user_impersonation"];


        if (user && user.userName) {
            authenticationParameters = {
                scopes: scope,
                loginHint: user.userName,
                authority: "https://login.microsoftonline.com/72f988bf-86f1-41af-91ab-2d7cd011db47/"
            };
        } else {
            authenticationParameters = {
                scopes: scope,
                authority: "https://login.microsoftonline.com/72f988bf-86f1-41af-91ab-2d7cd011db47/"
            };
        }
        return authenticationParameters;
    }

    public getTokenWithLoginPopup = (type: string): any => {
        const authenticationParameters = this.getAuthenticationParameters(type);
        return this.app.acquireTokenSilent(authenticationParameters).then(
            accessToken => {
                return accessToken.accessToken;
            }
        ).catch(error => {
            return this.getAccessToken(false, type).then(token => {
                return token;
            });
        }
        );
    }

    public async getAccessToken(promptConsent: boolean = false, type: string): Promise<string> {
        let scope = "https://campuswebapi.azurewebsites.net/user_impersonation";
        if (type === "graph")
            scope = "https://graph.microsoft.com/User.Read";
        else if (type === "site")
            scope = "https://microsoft.sharepoint.com/AllSites.Read";
        else if (type === "devops")
            scope = "499b84ac-1321-427f-aa17-267ca6975798/user_impersonation";
        return new Promise<string>((resolve, reject) => {
            microsoftTeams.authentication.authenticate({
                url: window.location.origin + "/auth-start.html?scope=" + scope,
                width: 600,
                height: 535,
                successCallback: (accessToken: string) => {
                    resolve(accessToken);
                },
                failureCallback: (reason) => {
                    reject(reason);
                }
            });
        });
    }
}
