import {Logging} from '../logging/logging';
import { GetEnvConfig } from "../config/GetEnvConfig";

export class CommonService {
    constructor() {
    }

    public ssListViewDateFormatforChild(date) {
        try {
            if (date && date.length > 0) {
                var formattedDate = new Date(date);
                var currentYear = new Date().getFullYear();
                return formattedDate.toLocaleString("en-us", { month: "short", day: "numeric", year: "numeric" }).replace(/\s/g, ' ');
            }
            else {
                return "";
            }
        }
        catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at ssListViewDateFormatforChild", error);
        }

    }

    public sormatLargenumberstoShort(num) {
        try {
            if (num >= 1000000000) {
                return (num / 1000000000).toFixed(1).replace(/\.0$/, '') + 'G';
            }
            if (num >= 1000000) {
                return (num / 1000000).toFixed(1).replace(/\.0$/, '') + 'M';
            }
            if (num >= 1000) {
                return (num / 1000).toFixed(1).replace(/\.0$/, '') + 'K';
            }
            return num;
        }
        catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at sormatLargenumberstoShort", error);
        }

    }


    public getFileTypeIcon(fileName: string){
        let iconPath = "";
        try{
            let fileType = fileName.split('.').pop();
            if(fileType != undefined){
                fileType = fileType.toLowerCase();
                switch(fileType){
                    case "aspx":
                        iconPath = "WebPage.png";
                        break;
                    case "html":
                        iconPath =  "html.png";
                        break;
                    case "xls":
                    case "xlsx":
                        iconPath = "xlsx.png";
                        break;
                    case "pdf":
                        iconPath = "pdf-16.png";
                        break;
                    case "pptx":
                        iconPath = "powerpoint-16.png";
                        break;
                    case "docx":
                    case "word":
                        iconPath = "Word.png";
                        break;
                    case "zip":
                        iconPath = "Zip.png";
                        break;
                    case "vsdx":
                        iconPath = "Visio.png"
                        break
                    default:
                        iconPath = "txt.png";
                        break;
                }
            }
        }
        catch(error){
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at getFileTypeIcon", error);
        }

        return iconPath;
    }
}