import * as React from "react";
import { IOfferData } from "../config/appdata";
import { Logging } from "../logging/logging";
import { GetEnvConfig } from "../config/GetEnvConfig";
import { Icon } from "office-ui-fabric-react";
import { CommonService } from '../findItBL/commonservice';


/**
 * State for the Tile View React component
 */
export interface ICustumTileSate {
    ShowAddToSEETeam: boolean;
    OfferResult?: IOfferData[];
    PageSize?: number;
    PageNo?: number;
    Index?: number;
}

/**
 * Properties for the Tile View React component
 */
export interface ICustumTileProps {
    offerResult?: IOfferData[];
    pageSize?: number;
    pageNo?: number;
    onAddToSEETeamClick(e: any, selectedData: IOfferData, showView: boolean): any;
    onSearchClick(e: any, searchText: string): any;
}

/**
 * Implementation of the Tile View React Component
 */
export class CustumTileViewCampusSeeTeams extends React.Component<ICustumTileProps, ICustumTileSate> {
    //to declare the class variable
    private tileContainer: any = null;
    private fileName: string = "CustumTileViewCampusSeeTeams.tsx";
    private commonService: CommonService;
    //constructor to initiiaise the state variable.    
    constructor(props: ICustumTileProps) {
        super(props);
        this.tileContainer = React.createRef();
        this.state = {
            ShowAddToSEETeam: false
            , OfferResult: this.props.offerResult
            , PageNo: this.props.pageNo
            , PageSize: this.props.pageSize
            , Index: -1
        };
        this.commonService = new CommonService();
    }

    //to set the state variable from the parent props
    public componentWillReceiveProps(nextProps: any) {
        this.setState({
            OfferResult: nextProps.offerResult, PageSize: nextProps.pageSize
            , PageNo: nextProps.pageNo
        });
    }

    //to register the mouse click event 
    public componentDidMount() {
        document.addEventListener("mousedown", this.handleClickOutside);
    }

    //to un-register the mouse click event 
    public componentWillUnmount() {
        document.removeEventListener("mousedown", this.handleClickOutside);
    }



    //to render the Tile View UI
    public render() {
        let currOfferResult = this.state.OfferResult === undefined ? [] : this.state.OfferResult;
        const pageSize = this.state.PageSize === undefined ? 10 : this.state.PageSize;
        const pageNo = this.state.PageNo === undefined ? 1 : this.state.PageNo;
        currOfferResult = currOfferResult.filter(obj => obj.SrNo >= ((pageSize * (pageNo - 1) + 1))
            && obj.SrNo <= (pageSize * pageNo));
        let tileViewUI: any = null;
        if (currOfferResult && currOfferResult.length > 0) {
            tileViewUI = currOfferResult.map((item, index) => {
                let customerName = item.CustomerNames.join(",");
                let domains = item.Domains.join(",");
                return (
                    <div className="campusSeeTeamTileView" style={{ display: "block" }}>
                        <div className="tile-contr campusSeeTeam_tiles">
                            <div className="tile-header" />
                            <div className="tileInner">
                                <a title={item.Title + '\n' + item.Description} href={item.URL}
                                    target="_blank" rel="noreferrer" className="outline OfferFocus ssOfferTitleValueClassName" >
                                    <h2 className="truncate-2lines">{item.Title}</h2>
                                    <p className="truncate-3lines">{item.Description}</p>
                                </a>
                                <div className="customerName  truncate-2lines">
                                    <a href="#" onClick={(e) => this.onSearchClick(e, customerName)}
                                        title={customerName}>{customerName}</a>
                                </div>
                                <div className="campusSeeTeamMiddle">
                                    {item.Docs && (item.Docs.length > 1 || (item.Docs.length == 1 && item.Docs[0].OID > 0)) ?
                                        <ul className="rclp-links">
                                            {
                                                item.Docs && item.Docs.length > 0 ?
                                                    item.Docs.map(o => <li title={o.Title}>
                                                        <a className="ssOfferTileDocumentsClassName" title={o.Title} href={o.DocURL} rel="noreferrer" target="_blank" >
                                                            <img alt="PowerPoint document" src={`../assets/${this.commonService.getFileTypeIcon(o.DocURL)}`} />
                                                        </a>
                                                    </li>)
                                                    : null
                                            }
                                        </ul> :
                                        <p className="truncate-3lines nodataClass"> No Documents </p>
                                    }
                                </div>
                                <div className="NewtileBottom">
                                    <p title={domains}>Domain : <span>{domains}</span></p>
                                    <p>
                                        {item.CodeLink && item.CodeLink.length > 0 ? <a target="_blank" href={item.CodeLink.split(',')[0]}>Code</a> : null}
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            });
        }
        return tileViewUI;
    }

    //to handle the search click event
    public onSearchClick(e: any, searchText: string) {
        e.stopPropagation();
        e.preventDefault();
        this.props.onSearchClick(e, searchText);
    }

    //to handle the click out side event 
    public handleClickOutside = (event: any) => {
        try {
            if (this.tileContainer.current && !this.tileContainer.current.contains(event.target)) {
                if (this.state.ShowAddToSEETeam) {
                    this.setState({
                        ShowAddToSEETeam: false,
                    });
                }
            }
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at handleClickOutside() in CustumTileViewCampusSeeTeams.tsx" + this.fileName, error);
        }
    }

    //to show the Add to SEE Team popup 
    public onShowAddToSEETeamClick(e: any, currIndex: number) {
        try {
            this.setState({
                ShowAddToSEETeam: true,
                Index: currIndex
            });
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onShowAddToSEETeamClick() in CustumTileViewCampusSeeTeams.tsx" + this.fileName, error);
        }
    }

    //to show the Add to SEE Team Menu click 
    public onShowAddToSEETeamMenuClick(e: any, index: number, item: IOfferData) {
        try {
            e.stopPropagation();
            e.preventDefault();
            this.setState({
                ShowAddToSEETeam: false,
                Index: -1
            });
            this.props.onAddToSEETeamClick(e, item, true);
        } catch (error) {
            Logging.AppInsightsTrackException(GetEnvConfig.AppSettings.appName, "Exception occured at onShowAddToSEETeamMenuClick() in CustumTileViewCampusSeeTeams.tsx" + this.fileName, error);
        }
    }
}
