import * as React from "react";
import { useState, useEffect } from "react";
import { useTeams } from "msteams-react-base-component";
import { ThemeProvider } from '@fluentui/react-theme-provider';
import { darkTheme, lightTheme } from '../../theme'
import { FindItTabHomePage } from "./FindItTabHomePage";
import * as microsoftTeams from "@microsoft/teams-js";

/**
 * Implementation of the Find It content page
 */
export const FindItTab = () => {

    const [{ inTeams, theme, context }] = useTeams();
    const [entityId, setEntityId] = useState<string | undefined>();
    const [useDarkMode, setUseDarkMode] = useState(false);
    useEffect(() => {
        if (inTeams === true) {
            microsoftTeams.authentication.authenticate({
                url: window.location.origin + "/auth-start.html",
                width: 600,
                height: 535,
                successCallback: (accessToken: string) => {
                    microsoftTeams.appInitialization.notifySuccess();
                },
                failureCallback: (message) => {
                    microsoftTeams.appInitialization.notifyFailure({
                        reason: microsoftTeams.appInitialization.FailedReason.AuthFailed,
                        message
                    });
                }
            });
        } else {
            setEntityId("Not in Microsoft Teams");
        }
    }, [inTeams]);

    useEffect(() => {
        if (context) {
            setEntityId(context.entityId);
        }
    }, [context]);

    /**
     * The render() method to create the UI of the tab
     */
    return (
        <ThemeProvider applyTo="body" theme={useDarkMode ? darkTheme : lightTheme}>
            {inTeams ?
                <FindItTabHomePage entityId={entityId} currPage={5} />
                : <></>
            }
        </ThemeProvider>
    );
};
